function focusMe(button) {
    //var elem = document.getElementsByClassName("btn_selection")[0];
    var elem = document.getElementsByClassName("btn_preselection")[0];
    console.log(elem);
    // if element having class `"button-selected"` defined, do stuff
    if (elem) {
        elem.className = "waves-effect waves-light btn btn_selection";

    }
    button.className = "waves-effect waves-light btn btn_preselection";
}